import React from 'react';
import { useSelector } from 'react-redux'

const ScoreBoard = () => {

  const { myScore, opponentScore, oya } = useSelector(state => state.switchHand)


  return (
    <table style={{fontsize: '20px'}}>
      <tbody>
      <tr>
        <th>{oya ? '' : ''}</th>
        <th>{oya ? '' : ''}</th>
      </tr>
      <tr>
        <th>{myScore}</th>
        <th>{opponentScore}</th>
      </tr>
      </tbody>
    </table>
  );
};

export default ScoreBoard;